.labels {
    display: flex;
    justify-content: center;
    
    .label{
        font-size: 12px;
        padding: 5px;
        border-radius: 20px;
        height: min-content;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        color: $clr2;
        margin: 5px;
        align-items: center;
    }

}

